
import React from 'react';
import { ShareButtons, generateShareIcon } from 'react-share';

const { FacebookShareButton, TwitterShareButton } = ShareButtons;

const FacebookIcon = generateShareIcon('facebook');
const TwitterIcon = generateShareIcon('twitter');

const Share = (props) => {
  const shareUrl = 'http://www.bristol.ac.uk';
  const title = 'Bristol University Course selector!';
  const Demo = {
    display: 'inline-block',
    textAlign: 'center',
    cursor: 'Pointer'
  };

  return (
    <div className={Demo}>
      <div style={Demo}>
        <FacebookShareButton
          url={shareUrl}
          title={title}
          picture={`https://goo.gl/BBUOxH`}
          description={props.description}
          className="Demo__some-network__share-button"
        >
          <FacebookIcon
            size={32}
            round
          />
        </FacebookShareButton>
      </div>
      &nbsp;&nbsp;&nbsp;
      <div style={Demo}>
        <TwitterShareButton
          url={shareUrl}
          title={props.description}
          hashtags={['UoB']}
          className="Demo__some-network__share-button"
        >
          <TwitterIcon
            size={32}
            round
          />
        </TwitterShareButton>
      </div>
    </div>
  );
};

Share.propTypes = {
  description: React.PropTypes.string
};

export default Share;



