import React from 'react';
import Styles from './header.css';
import Share from './share';

const Header = props => (
  <div>
    <div className={`${Styles.header}`}></div>
  <div className={`container`}>
    <div className="row" >
    <div className={`col-sm-3 ${Styles.logotainer}`} >
      <img src="http://www.bristol.ac.uk/media-library/sites/public-relations/images/logos/logo-uob.svg"
      className={Styles.logo}
      style={{ backgroundSize: 'contain', width: 190, height:80, marginTop:15, marginLeft: -30, backgroundColor: 'white', padding: 15 }}
     />
    </div>
    <div className="col-sm-6" style={{ textAlign: 'center', color:'white'}}>
      <h1 style={{ fontSize: 28, marginTop: 40 }}>Bristol University Course Selector</h1>
    </div>
      <div className="col-sm-2 col-sm-offset-1">
      <div className={Styles.share}>
      <Share description="Try the Bristol University course selector and see which course suits you!"/>
      </div>
      </div>
    </div>
  </div>
  </div>
)

export default Header;



