import React from 'react';
import { connect } from "react-redux";
import Share from '../components/share';
import Evidence from '../components/evidence';

const Results = props => {

    // TODO: move subjects into a reducer w/ rating and evidence switch statements

  let biomed = 0;
  let biochem = 0;
  let neuro = 0;
  let cmm = 0;
  let pharma = 0;
  let physio = 0;
  let biomedE = [];
  let biochemE = [];
  let neuroE = [];
  let cmmE = [];
  let pharmaE = [];
  let physioE = [];

console.log('biomed', biomed, 'biochem', biochem);
    // TODO: move subjects into a reducer w/ rating and evidence switch statements
    // temp fix.. scale factor on BIOMED answers...
  props.answers.map((ansobjs,i) => {
    for (let key in ansobjs)
      {
        if (ansobjs[key].course.indexOf('BIOMED') != -1)
        {
          biomed += (ansobjs[key].rating*0.2 || 0.2);
          biomedE.push(ansobjs[key].answer);
        }
        if (ansobjs[key].course.indexOf('BIOCHEM') != -1)
        {
          biochem += (ansobjs[key].rating || 1);
          biochemE.push(ansobjs[key].answer);
        }
        if (ansobjs[key].course.indexOf('CMM') != -1)
        {
          cmm += (ansobjs[key].rating || 1);
          cmmE.push(ansobjs[key].answer);
        }
        if (ansobjs[key].course.indexOf('NEURO') != -1)
        {
          neuro += (ansobjs[key].rating || 1);
          neuroE.push(ansobjs[key].answer);
        }
        if (ansobjs[key].course.indexOf('PHARMA') != -1)
        {
          pharma += (ansobjs[key].rating || 1);
          pharmaE.push(ansobjs[key].answer);
        }
        if (ansobjs[key].course.indexOf('PHYSIO') != -1)
        {
          physio += (ansobjs[key].rating || 1);
          physioE.push(ansobjs[key].answer);
        }
      }
  });

  // TODO: move subjects into a reducer w/ rating and evidence switch statements
  let subjects = [
    {course:'BIOMED', rating: biomed, evidence:biomedE, requisites:'Chemistry', image: 'https://goo.gl/CST5ee', url:'http://www.bristol.ac.uk/study/undergraduate/2017/cellular-molecular/bsc-biomedical-sciences/',
    name:'Biomedical Sciences (BSC)', description:'In the first year you will study subjects across the breadth of the biology related to human health and disease. This will provide a sound, broad background in biochemistry, cell and cancer biology, immunology, microbiology, neuroscience, pharmacology, physiology and virology.'},
    {course:'BIOCHEM', rating: biochem, evidence:biochemE, requisites:'Chemistry', image:'https://goo.gl/Mw8vmD', url:'http://www.bristol.ac.uk/study/undergraduate/2017/biochemistry/',
    name:'Biochemistry', description:'We provide a number of biochemistry courses and offer you the opportunity to spend a year in an industrial or research placement as part of your degree. Our courses offer a great deal of flexibility and choice, allowing you to explore a wide range of subjects and tailor your degree to your own interests.'},
    {course:'CMM', rating: cmm, evidence:cmmE, requisites:'Chemistry', image:'https://goo.gl/eXFiaY', url:'http://www.bristol.ac.uk/study/undergraduate/2017/cellular-molecular/',
    name:'Cellular and Molecular Medicine', description:'Taught by world-class research scientists and clinicians, you will have the opportunity to complete your own research project which reflects our research interests in cancer biology, stem-cell biology and regenerative medicine, or infection and immunology.'},
    {course:'NEURO', rating: neuro, evidence:neuroE, image:'https://goo.gl/Lqdgni', url:'http://www.bristol.ac.uk/study/undergraduate/2017/neuroscience/',
    name:'Neuroscience', description:'Our neuroscience degrees are run by the School of Physiology, Pharmacology and Neuroscience, within which there are world-leading research groups in neuroscience. Research-active staff are involved in teaching from year one, providing a high-quality student experience. You will be taught by exceptional academics using innovative teaching methods in specialised labs. Alongside practical experience, you will also develop the critical thinking and report-writing skills that are invaluable for any future career.'},
    {course:'PHARMA', rating: pharma, evidence:pharmaE, requisites:'Chemistry', image:'https://goo.gl/MjBJKR', url:'http://www.bristol.ac.uk/study/undergraduate/2017/pharmacology/',
    name:'Pharmacology', description:'Pharmacology brings together physiology, biochemistry, molecular biology and neuroscience. You will learn what drugs are, how they work and what they do. You will experience the approaches used to design and develop new drugs by investigating the effects of substances on single cells, organs and the whole body.'},
    {course:'PHYSIO', rating: physio, evidence:physioE,image:'https://goo.gl/OqWnzY', url:'http://www.bristol.ac.uk/study/undergraduate/2017/physiology/',
    name:'Physiology', description: 'Physiology is the study of body function - how cell tissue and organ function are integrated in the whole individual. It is an experimental, scientific discipline of general educational value.\n The School of Physiology, Pharmacology and Neuroscience houses world-leading research groups in cardiovascular physiology, neuroscience and cell biology, and from year one you will benefit from teaching from our research-active staff.'},
  ];

  // make into state selector function
  const filteredSorted = subjects.filter((a) => {
    if (a.requisites && (a.requisites === 'Chemistry'))
    {
        if (props.userSubjects.Chemistry === true)
        {
          return true;
        }
        else {
          return false;
        }
    }
    else {
      return true;
    }
  });

  const sortedSubjects = filteredSorted.sort((a,b) => (a.rating < b.rating) ? 1 : ((b.rating < a.rating) ? -1 : 0) );

  return (
    <div className="container">
      <div className="alert alert-success" style={{ fontSize: 17 }} >
        <strong>Congratulations!</strong> you finished!!
      </div>

      <div className="row">
        <div className="col-xs-6">
          <hr />
          <h1>Your top course is {sortedSubjects[0].name}</h1>
          <a href={sortedSubjects[0].url} target="_blank">
            <button style={{ fontSize: 17 }}  className="btn btn-success">Find out more!</button>
          </a>
          <hr />
        </div>
        <div className="col-xs-6" >
          <img style={{ width:'100%' }} src={`${sortedSubjects[0].image}`} />
        </div>
      </div>

      <div className="row">
        <div className="col-xs-12">
          <h3>{sortedSubjects[0].description}</h3>
          <hr />
          <Evidence evidence={sortedSubjects[0].evidence} />
        </div>
      </div>

      <div
        className="row alert alert-info"
        style={{ textAlign: 'center', marginTop: 20, paddingBottom: 20, fontSize: 17 }}
      >
        <h4>Now share the course selector with you're friends, and see if you get the same course!</h4>
        <Share
          topCourse={sortedSubjects[0]}
          description={`My top course came out as ${sortedSubjects[0].name} when I played the University of Bristol course selector, what's yours?`} />
      </div>

      <div className="row" style={{ textAlign: 'center'}}>
        <div className="col-xs-12">
          <hr/>
          <h2>you may also be interested in these courses:</h2>
          <hr/>
        </div>
      </div>

    { sortedSubjects.slice(1,4).map((obj, i) => {
      return (
        <div className="container" key={i}>
          <div className="row">
            <div className="col-xs-6">
              <h2>{obj.name}</h2>
              <h4>{obj.description}</h4>
              <a href={obj.url} target="_blank">
                <button
                  style={{ fontSize: 17 }}
                  className="btn btn-success"
                >
                  Find out more!
                </button>
              </a>
            </div>
            <div className="col-xs-6">
              <img style={{ width:'100%' }} src={`${obj.image}`} />
            </div>
          </div>
          <hr />
        </div>
      )
    }
    )}

  </div>
  )
};

const mapStateToProps = state => ({
  answers: state.answers,
  userSubjects: state.userSubjects
});

// connect hooks up mapStateToProps and mapDispatchToProps to named component
export default connect(mapStateToProps)(Results);



