import React from 'react';

class Evidence extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    }
  }

  handleClick() {
    this.setState({
      show: this.state.show ? false : true
    });
  }

  render() {
    return (
    <div>
      <h4>Why we thought you would enjoy this course:
        <button
          onClick={() => this.handleClick()}
          className='btn btn-primary'
        >
          show me
        </button>
      </h4>
      {this.state.show
        ? this.props.evidence.map((e,i) => <h4 key={e + i}>{e}</h4> ) : null }
    </div>
  )
  }

}

export default Evidence;



