import React from "react";

const Subject = props => (
  <div className="row" >
    <div className="checkbox" style={{ fontSize: 17 }}>
      <input
        style={{ width: 16, height: 16 }}
        type="checkbox"
        onClick={() => props.onClick()}
        id={props.subject}
      />
      <label htmlFor={props.subject}>&nbsp;&nbsp;&nbsp;A level&nbsp;
        <strong>{props.subject}</strong> or equivilant Level 3 HTQ
      </label>
    </div>
  </div>
);

Subject.propTypes = {
  subject: React.PropTypes.string,
  onClick: React.PropTypes.func
};

export default Subject;



