import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { userAnswer, goBack } from '../actions/actions';
import Styles from './ratinganswer.css';
import ReactDOM from 'react-dom';

class RatingAnswer extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      radio: {},
      attempted: false
    };
  }

  componentDidMount() {
    if (Object.keys(this.props.userSubjects).length == 0) {
          this.props.router.push('/subjects');
    }
    if (this.props.storedAnswers[this.props.currentQuestion])
      {
        this.setState({
          radio: this.props.storedAnswers[this.props.currentQuestion],
          attempted: false
        });
      }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.storedAnswers[nextProps.currentQuestion] !== undefined)
      {
        this.setState({
          radio: nextProps.storedAnswers[nextProps.currentQuestion],
          attempted: false
        });
      }
    else {
      this.setState({
        radio: {},
        attempted: false
      });
    }
  }

  render() {
    return (
  <div className="" style={{ marginLeft: '5!important' }}>

    <ReactCSSTransitionGroup
      transitionName={Styles}
      transitionAppear
      transitionAppearTimeout={1000}
      transitionEnterTimeout={1000}
      transitionLeaveTimeout={500}
    >
      <div key={`${this.props.currentQuestion}`} style={{ position: 'absolute', width: '80vw', fontSize: 17, left: '10vw' }} >
      {this.state.attempted === true ? <div className="alert alert-warning" style={{ marginTop: '20px'}} >Please answer <strong>all</strong> questions</div> : null}
      <div className={Styles.questiontainer}>
      <div className="row" style={{ marginBottom: 50 }}>
        <div>
          <h3 key={this.props.title}>{this.props.title}</h3>
        </div>
      </div>
    {this.props.answers.map((answerobj, i) => {
      return (
        <div key={answerobj.answer + 0} className={`${Styles.container}`} >
        <div className="row">
          <h4>
{this.state.attempted === true && this.state.radio[i] == null ? <span className="glyphicon glyphicon-asterisk" style={{ color: '#8a6d3b' }} ></span> : null }
    {answerobj.answer}</h4>
        </div>
        <div className="row">
          <div className="col-xs-2"></div>
          <div className="col-xs-8">
        {answerobj.ratings.map((rating) => {
          return (<div key={rating.name} className={`col-xs-${(12 / answerobj.ratings.length).toString()}`} >
            <div className="row">
            <label
              className={Styles.label}
              ><h5 className={Styles.labelFont} > {rating.name} </h5></label>
              </div>
              <input
                style={{ width: 16, height: 16 }}
                className={Styles.input}
                type="radio"
                onClick={() => this.handleClick(answerobj, i, rating.score)}
                name={answerobj.answer}
                defaultChecked={this.props.storedAnswers[this.props.currentQuestion] && (this.props.storedAnswers[this.props.currentQuestion][`${i}`].rating === rating.score) ? true : false}
              />
            </div>
          )
        })}
        </div>
        </div>
        <div className="row">
        <div className="col-xs-2"></div>
        <div className="col-xs-8">
        <hr />
      </div>
      </div>
    </div>
      );
    }
  )}
  <div className={`row buttons`} style={{ marginTop: 50 }}>
    <div className="col-xs-offset-4 col-xs-2">
{ this.props.currentQuestion > 0 ?  <button style={{ fontSize: 17,  marginLeft: -10  }} className={`btn btn-primary`} onClick={() => this.props.goBack()}>Back</button> : null}
    </div>
    <div className="col-xs-2">
  <button
    style={{ fontSize: 17, marginLeft: 10 }}
    onClick={() => this.handleSubmit()}
    className={`btn ${this.props.currentQuestion + 1 === this.props.questions.length ? 'btn-success' : 'btn-primary'}`}
  >
    {this.props.currentQuestion + 1 === this.props.questions.length ? 'Finish!' : 'Next'}
  </button>
</div>
  </div>
  <div className="row" style={{ marginTop: 10 }}>
      <h5 className={Styles.pageNumber}>PAGE: {this.props.currentQuestion + 1} / {this.props.questions.length}</h5>
  </div>
  </div>
          </div>

  </ReactCSSTransitionGroup>

  </div>
);}

handleClick(answer, key, rating) {
  this.setState({
          ...this.state,
              radio: {
              ...this.state.radio,
      [`${key}`]: {
        answer: answer.answer,
        course: answer.course,
        rating: rating
        }
      }
  });
  console.log('state', this.state);
}

handleSubmit() {
  if (Object.keys(this.state.radio).length === this.props.answers.length)
    {
      this.props.userAnswer(this.state.radio, this.props.currentQuestion);
      if (this.props.currentQuestion + 1 === this.props.questions.length) {
        window.scrollTo(0, 0);
      }
      else {
        setTimeout(() => window.scrollTo(0, 0), 500);
      }
    }
  else {
    this.setState({
          ...this.state,
          attempted: true
        });
       window.scrollTo(0, 150);
  }
  console.log('state', this.state);
}

};

const mapDispatchToProps = (dispatch) => {
  return {
    userAnswer(answer, question) {
      dispatch(userAnswer(answer, question));
    },
    goBack() {
      dispatch(goBack());
    },
  };
};

const mapStateToProps = state => ({
  storedAnswers: state.answers,
  currentQuestion: state.currentQuestion,
  questions: state.questions,
  userSubjects: state.userSubjects
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RatingAnswer));



