import React from 'react';
import Department from './department';

const Landing = props => (
  <div className="container" style={{ paddingBottom: 20 }}>
    <div className="row">
      <div className="col-sm-6">
        <Department
          name="Biomedical Sciences"
          subjects="Biology, Human Biology, Chemistry, Maths"
          description="Welcome to the Faculty of Biomedical Sciences, where scientists and students are involved in research and education about the fundamental principles that underpin clinical practice and shape our understanding of human and animal function."
          link="subjects"
          image="http://www.bristol.ac.uk/media-library/sites/study/undergraduate/images/course/Biomedical-Sciences-web.jpg"
          internal
        />
      </div>
      <div className="col-sm-6">
        <Department
          name="Sciences"
          subjects="Physics, Biology, Human Biology, Chemistry, Maths"
          description="The Faculty of Science comprises seven schools whose teaching and research activities span mathematical, physical, biological and environmental sciences. Our ethos is shaped by our ambition for academic activity."
          link="http://www.bristol.ac.uk/science/"
          image="http://www.bristol.ac.uk/media-library/sites/science/images/about-main-650x271.jpg"
          internal={false}
        />
      </div>
    </div>
    <hr />
    <div className="row">
      <div className="col-sm-6">
        <Department
          name="Health sciences"
          subjects="Physics, Biology, Human Biology, Chemistry, Maths"
          description="High quality innovative teaching supported by NHS partnerships and clinical expertise. Research excellence at every scale, from cellular mechanisms to health service provision."
          link="http://www.bristol.ac.uk/health-sciences/"
          image="http://www.bristol.ac.uk/media-library/sites/health-sciences/images/hp-main-responsive.jpg"
          internal={false}
        />
      </div>
      <div className="col-sm-6">
        <Department
          name="Engineering"
          subjects="Physics, Biology, Human Biology, Chemistry, Maths"
          description="Engineering at the University of Bristol is committed to producing leaders and entrepreneurs of the future and to advancing the knowledge and technological innovations required to address global challenges."
          link="http://www.bristol.ac.uk/engineering/"
          image="http://www.bristol.ac.uk/media-library/sites/engineering/migrated/images/simulator02new.png"
          internal={false}
        />
      </div>
    </div>
  </div>
);

export default Landing;



