import { createStore, applyMiddleware, compose } from "redux";
import logger from "redux-logger";
import Reducer from "./reducers/index";
import { loadState, saveState } from "./localStorage";

// configureStorey creates store from root reducer, persistedState allows state
// to be stored in browser local storage, applying middleware logger (for dev)
// and thunk (allows asynchronous actions i.e AJAX)

const configureStore = () => {
const persistedState = loadState();
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(Reducer, persistedState, composeEnhancers(

    applyMiddleware(logger())
  ));
  store.subscribe(() => {
    saveState({
      questions: store.getState().questions,
      currentQuestion: store.getState().currentQuestion,
      answers: store.getState().answers,
      userSubjects: store.getState().userSubjects
  })});

  return store;
};

export default configureStore;



