import React from "react";
import { Link } from "react-router";
import { connect } from "react-redux";
import { userSubjects } from '../actions/actions';
import Subject from '../components/subject';


class SubjectsForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      warning: false,
      subjects: {
        Biology: false,
        Chemistry: false,
        Mathematics: false,
        HumanBiology: false,
        Physics: false
      },
    };
  }

  handleClick(subject) {
    this.setState({
      ...this.state,
      subjects: {
        ...this.state.subjects,
        [`${subject}`]: !this.state.subjects[`${subject}`]
      }
    });
  }

  handleSubmit() {
    if (
      Object.keys(this.state.subjects).filter((key) => {
          return this.state.subjects[key] ? this.state.subjects[key] : false ;
      }).length >= 2) {
      this.props.userSubjects(this.state.subjects);
      this.props.router.push('quiz');
    } else {
      this.setState({
        ...this.state,
        warning: true
      });
    }
  }

  render() {
    return (
      <div className="container">
        {this.state.warning
          ? <div className="alert alert-success" style={{ marginTop: '20px' }}>
              Hello, you may be interested in other subjects offered
              by faculties on other <Link to="/"><strong>departments</strong></Link>,
               unfortunately all subjects require atleast two science A-levels
             </div> : null}
        <div className="row" >
          <div className="col-xs-8 col-xs-offset-2">
            <h1>Which subjects are you taking</h1>
            {Object.keys(this.state.subjects).map((subject) => {
              return (
                <Subject key={subject} subject={subject} onClick={() => this.handleClick(subject)} />
              );
            })}
            <div className="row" style={{ textAlign: 'center', marginTop: 30 }}>
              <button
                style={{ fontSize: 17 }}
                className="btn btn-primary"
                onClick={() => this.handleSubmit()}
              >
                SUBMIT
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    userSubjects(subjects) {
      dispatch(userSubjects(subjects));
    },
  };
};

export default connect(null, mapDispatchToProps)(SubjectsForm);



