import React from 'react';
import { Link } from 'react-router';
import Styles from './department.css';

const Department = props => (
  <div className={`${Styles.container}`}>
    <div className="row">
    <div className="col-sm-12">
    <h1>{props.name}</h1>
    </div>
    </div>
    <div className="row">
      <div className="col-sm-12">
      <img src={props.image} style={{ width: '100%', backgroundPosition: 'contain' }} />
    </div>
    </div>
    <div className="row">
      <div className="col-sm-12">
    <h3>{props.description}</h3>
    {props.internal === true
      ? <Link to={props.link}><button className="btn btn-primary" style={{ fontSize: 20 }}> Take the Quiz!</button></Link>
      : <a href={props.link} target="_blank"><button className="btn btn-default" style={{ fontSize: 20 }}> Find out more </button></a>}
  </div>
  </div>
  </div>
);

export default Department;



